import React, {useState} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import SystemUpdateAltIcon from '@material-ui/icons/SystemUpdateAlt';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import {SwipeableDrawer} from "@material-ui/core";
import {faDownload} from "@fortawesome/free-solid-svg-icons";
import {SidePanelTabs} from "./Components";


/**
 *
 * @param {JSX.Element} props.icon
 * @param {String} props.label
 * @param {String} props.selected_tab
 * @param {Function} props.setSelectedTab
 * @returns {JSX.Element}
 * @constructor
 */
function SidePanelItem(props) {
    const {icon, label, selected_tab, setSelectedTab} = props;
    return <ListItem
        button
        onClick={() => setSelectedTab(label)}
        className={selected_tab === label ? 'panel-item panel-item-active' : 'panel-item'}
    >
        <ListItemIcon>{icon}</ListItemIcon>
        <ListItemText primary={label}/>
    </ListItem>
}


/**
 *
 * @param {String} props.selected_tab
 * @param {Function} props.setSelectedTab
 * @returns {JSX.Element}
 * @constructor
 */
export default function RequiemDrawer(props) {
    const {selected_tab, setSelectedTab} = props;
    const [open, setOpen] = useState(true);

    // noinspection HtmlUnknownTarget
    return <React.Fragment>
        <SwipeableDrawer
            className="admin-drawer"
            anchor="left"
            open={open}
            variant="permanent"
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
        >
            <div role="presentation">
                <List>
                    <img className="cr-logo" src="/images/cyber-requiem-logo-magenta.png"/>
                </List>
                <Divider/>
                <List>
                    <SidePanelItem
                        icon={<DashboardIcon/>}
                        label={SidePanelTabs.dashboard}
                        selected_tab={selected_tab}
                        setSelectedTab={setSelectedTab}
                    />
                    <SidePanelItem
                        icon={<SystemUpdateAltIcon/>}
                        label={SidePanelTabs.scrapers}
                        selected_tab={selected_tab}
                        setSelectedTab={setSelectedTab}
                    />
                    <SidePanelItem
                        icon={<FontAwesomeIcon icon={faDownload} className="font-awesome-icon"/>}
                        label={SidePanelTabs.torrents}
                        selected_tab={selected_tab}
                        setSelectedTab={setSelectedTab}
                    />
                </List>
                <Divider/>
                <List>
                    {/*<SidePanelItem*/}
                    {/*    icon={<InboxIcon/>}*/}
                    {/*    label={SidePanelTabs.dashboard}*/}
                    {/*    selected_tab={selected_tab}*/}
                    {/*    setSelectedTab={setSelectedTab}*/}
                    {/*/>*/}
                </List>
            </div>
        </SwipeableDrawer>
    </React.Fragment>;
}