import React, {useState} from "react";

import moment from "moment";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faTimes} from "@fortawesome/free-solid-svg-icons";
import {CircularProgress, Hidden} from "@material-ui/core";
import { CircularProgressbar } from 'react-circular-progressbar';
import Grid from "@material-ui/core/Grid";
import Countdown from "react-countdown";
import Moment from "react-moment";
import 'react-circular-progressbar/dist/styles.css';

import {TARGET_ANIDB, TARGET_HENTAIGAMER, TARGET_MAL, TARGET_NYAA, TYPE_SCRAPE} from "../../../globals";
import {Report, Requiem, Target} from "../../../models";
import {useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";


function getLogoFromTarget(target) {
    switch (target) {
        case TARGET_MAL:
            return '/images/logos/mal_logo.png'
        case TARGET_ANIDB:
            return '/images/logos/anidb_logo.png'
        case TARGET_HENTAIGAMER:
            return '/images/logos/hentaigamer_logo.png'
        case TARGET_NYAA:
            return '/images/logos/nyaa_logo.png'
        default:
            return ''
    }
}


function isScrapeUpToDate(last, threshold) {
    if (last) {
        const today = moment()
        const prev = moment(last)
        const diff = today.diff(prev, 'days')
        return diff <= threshold
    }
    return false
}


/**
 *
 * @param {Object} props.report
 * @returns {JSX.Element}
 * @constructor
 */
function FormatScraperProgress(props) {
    const {report} = props;
    let progress;

    if (report === undefined)
        return <CircularProgress color="secondary"/>

    if (report[Report.type] === TYPE_SCRAPE)
        progress = <CircularProgressbar
            className="scraper-progress"
            maxValue={report[Report.finish_threshold]}
            minValue={0}
            value={report[Report.progress]}
            text={`${report[Report.progress]}/${report[Report.finish_threshold]}`}
        />
    else
        progress = <CircularProgressbar
            className="scraper-progress"
            value={report[Report.progress]}
            text={`${report[Report.progress]}%`}
        />

    return report[Report.progress] ?
        progress :
        <CircularProgress color="secondary"/>
}


/**
 *
 * @param {Object} props.requiem
 * @param {Object} props.target
 * @param {Array} props.active_reports
 * @returns {JSX.Element}
 * @constructor
 */
export function TargetSummary(props) {
    const {requiem, target, active_reports} = props
    const logo_url = getLogoFromTarget(target[Target.target])
    const scrape_up_to_date = isScrapeUpToDate(target[Target.last_scrape], requiem[Requiem.scrape_threshold]);
    const seasonal_up_to_date = isScrapeUpToDate(target[Target.last_seasonal_update], requiem[Requiem.seasonal_threshold]);
    const update_up_to_date = isScrapeUpToDate(target[Target.last_update], requiem[Requiem.update_threshold]);
    const xsDown = useMediaQuery(useTheme().breakpoints.down('xs'));

    let reports = {}
    active_reports.forEach(report => (
        reports[report[Report.active_target]] = report
    ))

    return <Grid item xs={12} sm={8} md={6} lg={4} xl={3} className="target-summary-card">
        <div className={target[Target.active] ? 'content-container' : 'content-container content-container-inactive'}>
            <div className="title-container">
                <img src={logo_url} className="target-logo"/>
                <p className="target-name">{target[Target.target]} - <span>{target[Target.branch]}</span></p>
            </div>
            <Grid container>
                <Grid item xs={12} sm={target[Target.active] && reports[target[Target.id]] !== undefined ? 7 : 12}
                      className="info-container"
                >
                    <p className="margin-top-5">
                        <span>Status:</span> {target[Target.active] ?
                        <span className="color-green">Active</span> :
                        <span className="color-gray">Inactive</span>}
                    </p>
                    {target[Target.last_scrape] ? <p className="margin-top-5">
                        <span>Last Scrape:</span>{' '}
                        <Moment format="Do MMM YYYY" date={target[Target.last_scrape]}/>{' '}
                        {scrape_up_to_date ?
                            <FontAwesomeIcon icon={faCheck} className="color-green" title={'Up to date'}/> :
                            <FontAwesomeIcon icon={faTimes} className="color-danger" title={'Not up to date'}/>}
                    </p> : null}
                    {target[Target.last_seasonal_update] ? <p>
                        <span>Last Seasonal:</span>{' '}
                        <Moment format="Do MMM YYYY" date={target[Target.last_seasonal_update]}/>{' '}
                        {seasonal_up_to_date ?
                            <FontAwesomeIcon icon={faCheck} className="color-green" title={'Up to date'}/> :
                            <FontAwesomeIcon icon={faTimes} className="color-danger" title={'Not up to date'}/>}
                    </p> : null}
                    {target[Target.last_update] ? <p>
                        <span>Last Update:</span>{' '}
                        <Moment format="Do MMM YYYY" date={target[Target.last_update]}/>{' '}
                        {update_up_to_date ?
                            <FontAwesomeIcon icon={faCheck} className="color-green" title={'Up to date'}/> :
                            <FontAwesomeIcon icon={faTimes} className="color-danger" title={'Not up to date'}/>}
                    </p> : null}
                </Grid>
                {target[Target.active] && reports[target[Target.id]] !== undefined ?
                    <Grid item xs={12} sm={5} className="chart-container"
                          style={xsDown ? {marginTop: '1rem'} : undefined}
                    >
                        <FormatScraperProgress report={reports[target[Target.id]]}/>
                        <p>
                            {reports[target[Target.id]]?.[Report.finished] ?
                                <span className="color-secondary">Finished</span> :
                                reports[target[Target.id]]?.[Report.error] ?
                                    <span className="color-danger">Error</span> :
                                    <span className="color-success">Running..</span>}
                        </p>
                        {target[Target.type] ? <p><span>Type:</span> {target[Target.type]}</p> : null}
                        {reports[target[Target.id]]?.[Report.eta_seconds] ?
                            <p><span>ETA:</span> <Countdown
                                date={Date.now() + (reports[target[Target.id]]?.[Report.eta_seconds] * 1000)} />
                            </p> : null}
                    </Grid> : null}
                {target[Target.active] && reports[target[Target.id]]?.[Report.message] ?
                    <Grid item xs={12} className="message-container">
                        <p title={reports[target[Target.id]]?.[Report.message]}>
                            MSG: {reports[target[Target.id]]?.[Report.message]}
                        </p>
                    </Grid> : null}
            </Grid>
        </div>
    </Grid>
}
