import React, {Component} from 'react'
import {connect} from 'react-redux'

import Grid from '@material-ui/core/Grid';
import {Anime, AnimeFranchise} from "../../../../models";
import {getAnimeFranchise} from "../../../../actions/anime";
import {
    Timeline, TimelineConnector,
    TimelineContent,
    TimelineDot,
    TimelineItem,
    TimelineOppositeContent,
    TimelineSeparator
} from "@material-ui/lab";
import {isEmpty} from "../../../functions";
import TitleCard from "../../../layout/cards/TitleCard";
import {ANIME_STATUSES, BRANCH_ANIME} from "../../../../globals";
import Loading from "../../../layout/common/Loading";
import Typography from "@material-ui/core/Typography";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {AnimeStatuses} from "../../utils";
import {CyberButton} from "../../../layout/common/common";
import {
    faArrowAltCircleRight,
    faArrowDown,
    faArrowRight,
    faArrowUp,
    faCalendarAlt
} from "@fortawesome/free-solid-svg-icons";


function getDotClassnames(status) {
    if (status === AnimeStatuses.finished_airing) return 'timeline-dot timeline-dot-primary'
    if (status === AnimeStatuses.not_yet_aired) return 'timeline-dot timeline-dot-gray'
    if (status === AnimeStatuses.currently_airing) return 'timeline-dot timeline-dot-success'
    return 'timeline-dot'
}


class AirOrderTab extends Component {

    constructor() {
        super(...arguments);
        this.updateOrdering = this.updateOrdering.bind(this)
        this.state = {
            newest_first: true
        };
    }

    componentDidMount() {
        this.props.getAnimeFranchise(this.props.anime[Anime.franchise])
    }

    updateOrdering() {
        this.setState({newest_first: !this.state.newest_first})
    }

    render() {
        let titles = this.props.anime_franchise[AnimeFranchise.anime_franchise]
        if (titles && titles.length > 0)
            titles = titles.sort((a, b) => new Date(b['air_start']) - new Date(a['air_start']))

        if (!this.state.newest_first)
            titles = titles.reverse()

        return this.props.anime_franchise_loading ? <Loading text={'Franchise Loading'}/> :
            <Grid container className="air-order-tab">
                <Grid item xs={12} className="ordering-container">
                    {this.state.newest_first ?
                        <p>Newest <FontAwesomeIcon icon={faArrowRight}/> Oldest</p> :
                        <p>Oldest <FontAwesomeIcon icon={faArrowRight}/> Newest</p>
                    }
                    <CyberButton onClick={this.updateOrdering} variant="contained" classNames="ordering-btn btn-contained-primary" title={'Sort by '}>
                        <FontAwesomeIcon icon={faCalendarAlt}/>{' '}
                        <FontAwesomeIcon icon={this.state.newest_first ? faArrowDown : faArrowUp}/>
                    </CyberButton>
                </Grid>
                <Grid item xs={12} className="timeline-container">
                    <Timeline align="alternate">
                    {titles?.map((title, x) => <TimelineItem key={title[Anime.id]}>
                        <TimelineOppositeContent className={!(x % 2) ? 'right-content timeline-item-container' : 'timeline-item-container'}>
                            <TitleCard
                                branch={BRANCH_ANIME}
                                title={title}
                                user_buttons={true}
                                user_status={!isEmpty(this.props.user_entry_statuses) ?
                                    this.props.user_entry_statuses[`${title[Anime.id]}`] : null}
                            />
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                            <TimelineDot
                                variant={title[Anime.status] === AnimeStatuses.not_yet_aired ? 'outlined' : 'default'}
                                className={getDotClassnames(title[Anime.status])}
                            />
                            {x + 1 < titles.length ? <TimelineConnector/> : null}
                        </TimelineSeparator>
                        <TimelineContent className="timeline-date">
                            <p>{title[Anime.air_start]}</p>
                        </TimelineContent>
                    </TimelineItem>)}
                </Timeline>
                </Grid>
            </Grid>
    }
}

const mapStateToProps = state => ({
    anime: state.anime.anime,
    anime_franchise_loading: state.anime.anime_franchise_loading,
    anime_franchise: state.anime.anime_franchise,
    user_entry_statuses: state.users.user_anime_entry_statuses,
});

export default connect(mapStateToProps, {getAnimeFranchise})(AirOrderTab)
