import React, {useState} from 'react'


function getClassFromScore(score) {
    switch (score) {
        case 1:
            return 'score-one-badge'
        case 2:
            return 'score-two-badge'
        case 3:
            return 'score-three-badge'
        case 4:
            return 'score-four-badge'
        case 5:
            return 'score-five-badge'
        case 6:
            return 'score-six-badge'
        case 7:
            return 'score-seven-badge'
        case 8:
            return 'score-eight-badge'
        case 9:
            return 'score-nine-badge'
        case 10:
            return 'score-ten-badge'
        default:
            return 'color-gray'
    }
}


/**
 *
 * @param {Number} props.score
 * @returns {JSX.Element}
 * @constructor
 */
export function UserScoreBadge(props) {
    const classname = getClassFromScore(props.score)

    return <span className={classname}>
        {props.score === 0 ? '-' : props.score}
    </span>
}