import React, {Component} from 'react'
import {connect} from 'react-redux'

import {User} from "../../../models";
import MenuAppBar from "./CyberHeader";
import PropTypes from "prop-types";


class CyberNavbar extends Component {
    static propTypes = {
        /** classNames */
        classNames: PropTypes.object,
    };

    render() {
        return <MenuAppBar classNames={this.props.classNames} user_authenticated={this.props.user_authenticated}
                           user_id={this.props.user_id} username={this.props.username} superuser={this.props.superuser}
        />
    }
}

const mapStateToProps = state => ({
    user_authenticated: state.auth.user_authenticated,
    user_id: state.auth.user ? state.auth.user[User.id] : null,
    superuser: state.auth.user ? state.auth.user[User.is_superuser] : false,
    username: state.auth.user ? state.auth.user[User.username] : null
});

export default connect(mapStateToProps, {})(CyberNavbar)
