import React, {Component, useState} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Grid from "@material-ui/core/Grid";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
    faArrowDown,
    faClock,
    faDownload,
    faHdd,
    faPuzzlePiece,
    faUpload,
    faUsers
} from "@fortawesome/free-solid-svg-icons";

import {getActiveTorrentsProgress, getStorageInfo} from "../../../actions/requiem";
import {Anime, AnimeOnServer, ApiResult, StorageDisk} from "../../../models";
import {LinearProgress, TableCell} from "@material-ui/core";
import {getBytesToSize, TorrentProgressInfo} from "../snippets/snippets";
import {CyberButton} from "../../layout/common/common";
import {getPendingAnimeServerInfos} from "../../../actions/anime";
import {isEmpty} from "../../functions";
import TitleCard from "../../layout/cards/TitleCard";
import {BRANCH_ANIME, LANGUAGES} from "../../../globals";
import Loading from "../../layout/common/Loading";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import Table from "@material-ui/core/Table";
import Chip from "@material-ui/core/Chip";
import {
    AdminEditAnimeOnServerButton,
    ApproveAnimeOnServerButton,
    DeleteAnimeOnServerButton,
    OpenAnimeFolderButton
} from "../forms/forms";
import {AnimeOnServerModalForm} from "../forms/forms";
import {GET_PENDING_ANIME_SERVER_INFO} from "../../../actions/types";
import Pagination from "@material-ui/lab/Pagination";
import {playClickSound} from "../../layout/sounds";
import GoToPageDialog from "../../layout/pagination/goToPageDialog";
import {filterTitles} from "../../browse/functions";


export class TorrentFilter {
    static _name = 'name';
    static size = 'size';
    static dl_speed = 'dl_speed';
    static up_speed = 'up_speed';
    static remaining = 'remaining';
    static seeds = 'seeds';
    static leeches = 'leechs';
    static eta = 'eta';
}


/**
 *
 * @param {Object} props.storage
 * @returns {JSX.Element}
 * @constructor
 */
function StorageContainer(props) {
    const {storage} = props;

    let progress = ((storage[StorageDisk.used] / (1024 * 1024.0 * 1024.0)).toFixed(2) /
        (storage[StorageDisk.total] / (1024 * 1024.0 * 1024.0)).toFixed(2)).toFixed(3) * 100;

    return <Grid item xs={12} md={4} lg={3} xl={2} className="storage-container">
        <div className="inner-container">
            <FontAwesomeIcon icon={faHdd} className={storage[StorageDisk.active] ?
                progress < 90 ? 'storage-icon' : 'storage-icon-low-space' : 'storage-icon-inactive'}/>
            <p className="letter">{storage[StorageDisk.letter]}: {storage[StorageDisk.active] ?
                <span className="color-success">Running</span> :
                <span className="color-danger">Inactive</span>}
            </p>

            <LinearProgress className={progress < 90 ? 'storage-progress-secondary' : 'storage-progress-danger'}
                            variant="determinate" value={progress}/>
            <p className="total-size">
                {getBytesToSize(storage[StorageDisk.free])} <span className="color-gray">free of</span>{' '}
                {getBytesToSize(storage[StorageDisk.total])}
            </p>
        </div>
    </Grid>
}


/**
 *
 * @param {Object} props.torrent_filters
 * @param {Object} props.filters_key
 * @param {JSX.Element} props.icon
 * @param {String} props.btn_classname
 * @param {Function} props.setTorrentFilters
 * @returns {JSX.Element}
 * @constructor
 */
function TorrentClientFilterButton(props) {
    const [value, setValue] = useState(false)

    return <CyberButton
        classNames={props.torrent_filters[props.filters_key] ? 'btn-contained-secondary' : undefined}
        variant={props.torrent_filters[props.filters_key] ? 'contained' : undefined}
        onClick={() =>
            props.setTorrentFilters(
                {...props.torrent_filters, [props.filters_key]: !props.torrent_filters[props.filters_key]}
            )}
    >
        <FontAwesomeIcon icon={props.icon} className={props.btn_classname}/>
    </CyberButton>
}


/**
 *
 * @param {Array} props.active_torrents
 * @returns {JSX.Element}
 * @constructor
 */
function ActiveTorrents(props) {
    const {active_torrents} = props;
    const default_filters = {
        [TorrentFilter._name]: false,
        [TorrentFilter.size]: true,
        [TorrentFilter.dl_speed]: true,
        [TorrentFilter.up_speed]: true,
        [TorrentFilter.remaining]: true,
        [TorrentFilter.seeds]: true,
        [TorrentFilter.leeches]: true,
        [TorrentFilter.eta]: true
    };
    const [torrent_filters, setTorrentFilters] = useState(default_filters)

    return <>
        <Grid item xs={12} className="torrent-btn-group">
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.size}
                icon={faHdd}
                setTorrentFilters={setTorrentFilters}
            />
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.dl_speed}
                icon={faDownload}
                setTorrentFilters={setTorrentFilters}
            />
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.up_speed}
                icon={faUpload}
                setTorrentFilters={setTorrentFilters}
            />
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.remaining}
                icon={faPuzzlePiece}
                setTorrentFilters={setTorrentFilters}
            />
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.leeches}
                icon={faUsers}
                setTorrentFilters={setTorrentFilters}
                btn_classname={'color-success'}
            />
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.seeds}
                icon={faUsers}
                setTorrentFilters={setTorrentFilters}
                btn_classname={'color-danger'}
            />
            <TorrentClientFilterButton
                torrent_filters={torrent_filters}
                filters_key={TorrentFilter.eta}
                icon={faClock}
                setTorrentFilters={setTorrentFilters}
            />
        </Grid>
        <Grid item xs={12} lg={6} xl={4} className="active-torrents-container">
            {active_torrents.map(torrent => <TorrentProgressInfo torrent={torrent} filters={torrent_filters}/>)}
        </Grid>
    </>
}


function checkIfAllSharedTitlesAreApproved(titles) {
    for (let x = 0; x < titles.length; x++)
        if (!titles[x][Anime.anime_on_server][AnimeOnServer.approved])
            return false
    return true
}


/**
 *
 * @param {Object} props.pending_anime
 * @param {Function} props.getPendingAnimeServerInfos
 * @returns {JSX.Element}
 * @constructor
 */
function PendingAnime(props) {
    const {pending_anime} = props
    const [current_page, setCurrentPage] = useState(1)
    const [items_per_page, setItemsPerPage] = useState(5)
    const [page_dialog_open, setPageDialogOpen] = useState(false)

    if (isEmpty(pending_anime))
        return null

    const goToPage = (page) => {
        if (page > 0 && page <= Math.ceil(pending_anime[ApiResult.count] / items_per_page)) {
            window.scrollTo(0, 0);
            setCurrentPage(page);
            props.getPendingAnimeServerInfos(page)
        }
    }

    return <> {pending_anime[ApiResult.results]?.map(server_info => <Grid item xs={12} lg={6} xl={4}
                                                                      className="pending-container">
            <div className="pending-inner-container">
                <div className="title-wrapper">
                    <TitleCard
                        branch={BRANCH_ANIME}
                        title={server_info[AnimeOnServer.anime]}
                        mini={true}
                    />
                </div>
                <div className="info-wrapper">
                    <Table aria-label="pending table">
                        <TableBody>
                            <TableRow>
                                <TableCell component="th" scope="row">Location:</TableCell>
                                <TableCell align="right">{server_info[AnimeOnServer.location]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">Size:</TableCell>
                                <TableCell align="right">{server_info[AnimeOnServer.size_on_HDD_GB]}GB</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">Video:</TableCell>
                                <TableCell align="right">{server_info[AnimeOnServer.video_quality]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">Audio:</TableCell>
                                <TableCell align="right">{server_info[AnimeOnServer.audio_channel]}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">Dubs:</TableCell>
                                <TableCell align="right">{LANGUAGES.map(language => (
                                    server_info[language.toLowerCase() + '_dub'] ? <Chip
                                        key={server_info[AnimeOnServer.anime]?.[Anime.id]} variant="default" size="small"
                                        className="chip-secondary"
                                        label={language}
                                    /> : null
                                ))}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">Subs:</TableCell>
                                <TableCell align="right">{LANGUAGES.map(language => (
                                    server_info[language.toLowerCase() + '_sub'] ? <Chip
                                        key={server_info[AnimeOnServer.anime]?.[Anime.id]} variant="default" size="small"
                                        className="chip-secondary"
                                        label={language}
                                    /> : null
                                ))}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row">Notes:</TableCell>
                                <TableCell align="right">{server_info[AnimeOnServer.additional_info] ?
                                    server_info[AnimeOnServer.additional_info] : <span className="color-gray">No</span>}
                                </TableCell>
                            </TableRow>
                            {server_info[AnimeOnServer.shared_torrent_with].length > 0 ? null : <TableRow>
                                <TableCell component="th" scope="row">Shares:</TableCell>
                                <TableCell align="right" className="color-gray">No</TableCell>
                            </TableRow>}
                        </TableBody>
                    </Table>
                </div>
                <div className="title-btn-container">
                    <DeleteAnimeOnServerButton id={server_info[AnimeOnServer.anime][Anime.id]}/>
                    <OpenAnimeFolderButton id={server_info[AnimeOnServer.anime][Anime.id]}/>
                    <AnimeOnServerModalForm
                        id={server_info[AnimeOnServer.anime][Anime.id]} edit={true}
                        alternative_callback={GET_PENDING_ANIME_SERVER_INFO}
                    />
                    <AdminEditAnimeOnServerButton id={server_info[AnimeOnServer.anime][Anime.id]}/>
                    {checkIfAllSharedTitlesAreApproved(server_info[AnimeOnServer.shared_torrent_with]) ?
                        <ApproveAnimeOnServerButton
                            id={server_info[AnimeOnServer.anime][Anime.id]}
                            alternative_callback={GET_PENDING_ANIME_SERVER_INFO}
                        /> : null}
                </div>
                {server_info[AnimeOnServer.shared_torrent_with].length > 0 ? <div className="shared-titles-container">
                    <p>Shared anime</p>
                    <Table aria-label="pending table">
                        <TableBody>
                            {server_info[AnimeOnServer.shared_torrent_with].map(anime => (
                                <SharedTitle anime={anime}/>
                            ))}
                        </TableBody>
                    </Table>
                </div> : null}
            </div>
        </Grid>)}
        <div className="pagination">
            <Pagination
                count={Math.ceil(pending_anime[ApiResult.count] / items_per_page)}
                color="primary"
                siblingCount={3}
                page={current_page}
                onChange={(type, page) => {
                    playClickSound();
                    if (page !== current_page) goToPage(page);
                    else setPageDialogOpen(true)
                }}
            />
            <GoToPageDialog
                open={page_dialog_open}
                current_page={current_page}
                total_pages={Math.ceil(pending_anime[ApiResult.count] / items_per_page)}
                goToPage={goToPage}
                onClose={() => setPageDialogOpen(false)}/>
        </div>
    </>
}


/**
 *
 * @param {Object} props.anime
 * @returns {JSX.Element}
 * @constructor
 */
function SharedTitle(props) {
    const {anime} = props
    const [expand, setExpand] = useState(false)

    return <TableRow>
        <TableCell component="th" scope="row">
            <TitleCard
                branch={BRANCH_ANIME}
                title={anime}
                mini={true}
            />
        </TableCell>
        <TableCell align="center" className="title-btn-container">
            {anime[Anime.anime_on_server][AnimeOnServer.approved] ? <p className="color-success">Approved</p> :
                <p className="color-danger">Pending Approval</p>}
            <DeleteAnimeOnServerButton id={anime[Anime.id]}/>
            <OpenAnimeFolderButton id={anime[Anime.id]}/>
            <AnimeOnServerModalForm
                id={anime[Anime.id]} edit={true}
                alternative_callback={GET_PENDING_ANIME_SERVER_INFO}
            />
            <AdminEditAnimeOnServerButton id={anime[Anime.id]}/>
            {!anime[Anime.anime_on_server][AnimeOnServer.approved] ? <ApproveAnimeOnServerButton
                id={anime[Anime.id]}
                alternative_callback={GET_PENDING_ANIME_SERVER_INFO}
            /> : null}
        </TableCell>
    </TableRow>
}


function getTotalSizeAndFreeSpace(storage) {
    let total_size = 0;
    let total_free = 0;
    let anime_size = 0;
    let anime_free = 0;

    for (let x = 0; x < storage?.length; x++) {
        total_size += storage[x][StorageDisk.total];
        total_free += storage[x][StorageDisk.free];
        if (storage[x][StorageDisk.branch] === BRANCH_ANIME) {
            anime_size += storage[x][StorageDisk.total];
            anime_free += storage[x][StorageDisk.free];
        }
    }

    return [total_size, total_free, anime_size, anime_free]
}


export class Torrents extends Component {
    static propTypes = {};

    constructor() {
        super(...arguments);
        this.state = {
            get_active_torrents_interval: undefined
        };
    }

    componentDidMount() {
        window.scrollTo(0, 0);
        this.props.getStorageInfo()
        this.props.getPendingAnimeServerInfos()
        this.props.getActiveTorrentsProgress()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.get_active_torrents_interval === undefined)
            if (this.props.active_torrents.length > 0) {
                const interval = setInterval(this.props.getActiveTorrentsProgress, 2000);
                this.setState({get_active_torrents_interval: interval})
            }
    }

    componentWillUnmount() {
        if (this.state.get_active_torrents_interval !== undefined)
            clearInterval(this.state.get_active_torrents_interval)
    }

    render() {
        const [total_size, total_free, anime_size, anime_free] = getTotalSizeAndFreeSpace(this.props.storage);

        return <Grid container xs={12} id="Torrents">
            <Grid item xs={12} className="title-container">
                <p>Storage Status</p>
                <p><span className="color-gray">Storage:</span> {getBytesToSize(total_free)} free
                    of {getBytesToSize(total_size)}{' '}
                    <span className="color-gray">Anime:</span> {getBytesToSize(anime_free)} free
                    of {getBytesToSize(anime_size)}</p>
            </Grid>
            {this.props.storage.map(storage => <StorageContainer
                storage={storage}
            />)}
            <Grid item xs={12} className="title-container">
                <p>{this.props.active_torrents.length > 0 ? 'Active Torrents' : 'No Active Torrents'}</p>
            </Grid>
            {this.props.active_torrents.length > 0 ?
                <ActiveTorrents active_torrents={this.props.active_torrents}/> : null}
            <Grid item xs={12} className="title-container">
                <p>
                    Pending Approval <span className="color-gray">
                    ({this.props.pending_anime_server_infos[ApiResult.count]})</span>
                </p>
            </Grid>
            {this.props.anime_loading ? <Grid item xs={12} className="text-center"><Loading block={true}/></Grid> :
                <PendingAnime pending_anime={this.props.pending_anime_server_infos}
                              getPendingAnimeServerInfos={this.props.getPendingAnimeServerInfos}
                />}
        </Grid>
    }
}

const mapStateToProps = state => ({
    storage: state.requiem.storage,
    active_torrents: state.requiem.active_torrents,
    anime_loading: state.anime.anime_loading,
    pending_anime_server_infos: state.anime.pending_anime_server_infos,
});

export default connect(mapStateToProps, {
    getStorageInfo,
    getActiveTorrentsProgress,
    getPendingAnimeServerInfos
})(Torrents)
