import axios from "axios";

import {
    AUTH_USER,
    USER_LOADING,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    GET_USER,
    SET_REDIRECT_URL,
    UPDATE_USER_DISPLAY_SETTINGS_STORE,
    UPDATE_USER_LIST_DESIGN_SETTINGS_STORE,
    SET_OPEN_APPLICATIONS_STATUS
} from "./types";
import {
    API_AUTH_USER,
    API_LOGIN,
    API_REGISTER,
    API_LOGOUT,
    API_GET_USER,
    API_GET_OPEN_APPLICATIONS_STATUS
} from "./api_urls";
import {returnErrors} from "./messages";


// GET OPEN APPLICATIONS STATUS
export const getOpenApplicationsStatus = () => dispatch => {
    axios
        .get(API_GET_OPEN_APPLICATIONS_STATUS)
        .then(res => {
            dispatch({
                type: SET_OPEN_APPLICATIONS_STATUS,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// GET A SPECIFIC USER
export const getUser = id => (dispatch, getState) => {
    dispatch({type: USER_LOADING});
    axios
        .get(API_GET_USER(id), tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_USER,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


// CHECK TOKEN & LOAD USER (GET LOGGED-IN USER)
export const authUser = () => (dispatch, getState) => {
    dispatch({type: USER_LOADING});

    // noinspection JSUnusedLocalSymbols
    axios
        .get(API_AUTH_USER, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: AUTH_USER,
                payload: res.data
            });
        })
        .catch(err => {
            dispatch({
                type: AUTH_ERROR
            });
        });
};

// LOGIN USER
export const login = (username, password) => dispatch => {
    dispatch({type: USER_LOADING});

    // Headers
    const config = {
        headers: {
            'Content-Type': 'application/json',
        }
    };

    // Request Body
    const body = JSON.stringify({username, password});

    axios
        .post(API_LOGIN, body, config)
        .then(res => {
            dispatch({
                type: LOGIN_SUCCESS,
                payload: res.data
            });
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
            dispatch({
                type: LOGIN_FAIL
            });
        });
};

// REGISTER USER
export const register = (new_user) => dispatch => {
    dispatch({type: USER_LOADING});

    // Headers
    const config = {
        headers: {
            'Content-Type': 'application/json',
        }
    };

    // Request Body
    const body = JSON.stringify(new_user);

    axios
        .post(API_REGISTER, body, config)
        .then(res => {
            dispatch({
                type: REGISTER_SUCCESS,
                payload: res.data
            });
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
            dispatch({
                type: REGISTER_FAIL
            });
        });
};


// LOGOUT USER
export const logout = () => (dispatch, getState) => {
    // noinspection JSUnusedLocalSymbols
    dispatch({type: LOGOUT_SUCCESS})
    axios
        .post(API_LOGOUT, null, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: LOGOUT_SUCCESS
            });
        })
        .catch(err => {
            // if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            // else dispatch(returnErrors(err, 0));
        });
};


// setRedirectUrl
export const setRedirectUrl = pathname => dispatch => {
    dispatch({
        type: SET_REDIRECT_URL,
        payload: pathname
    });
};


// UPDATE USER ANIME DISPLAY SETTINGS
export const updateUserDisplaySettingsStore = (branch, settings) => dispatch => {
    dispatch({
        type: UPDATE_USER_DISPLAY_SETTINGS_STORE,
        payload: settings,
        branch: branch,
    });
};


// UPDATE USER ANIME DISPLAY SETTINGS
export const updateUserListDesignSettingsStore = (branch, settings) => dispatch => {
    dispatch({
        type: UPDATE_USER_LIST_DESIGN_SETTINGS_STORE,
        payload: settings,
        branch: branch,
    });
};


// Setup config with token - helper function
export const tokenConfig = getState => {
    const token = getState().auth.token;

    const config = {
        headers: {
            'Content-Type': 'application/json',
        }
    };

    if (token)
        config.headers['Authorization'] = `Token ${token}`;

    return config;
};
