import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Link as RouterLink} from 'react-router-dom';

import TextField from '@material-ui/core/TextField';
import {Form, Formik} from 'formik';

import {RegisterSchema, RegisterSchemaWithInvitationCode} from "./functions";
import {register} from '../../actions/auth'
import {LOGIN} from "../../urls";
import {CyberButton} from "../layout/common/common";
import {playClickSound, playHoverSound, playWarningSound} from "../layout/sounds";
import CircularProgress from "@material-ui/core/CircularProgress";


class RegisterForm extends Component {
    constructor() {
        super(...arguments);
        this.state = {};
    }

    render() {
        const field_names = {
            username: 'username',
            email: 'email',
            password: 'password',
            password2: 'password2',
            invitation_code: 'invitation_code'
        };

        const initial_values = {
            username: '',
            email: '',
            password: '',
            password2: '',
            invitation_code: '',
        };

        return !this.props.user_loading && this.props.open_applications_status !== undefined ? <Formik
            initialValues={initial_values}
            validationSchema={this.props.open_applications_status ? RegisterSchema : RegisterSchemaWithInvitationCode}
            onSubmit={(values, {setSubmitting}) => {
                const new_user = {
                    username: values.username,
                    password: values.password,
                    email: values.email,
                    invitation_code: values.invitation_code
                };
                this.props.register(new_user);
                setSubmitting(false);
            }}>
            {({errors, touched, values, handleChange, handleBlur}) => (
                <Form id="Register-Form">
                    <TextField
                        label="Username"
                        className="form-control"
                        value={values.username}
                        name={field_names.username}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        error={errors.username && touched.username}
                        helperText={(errors.username && touched.username) && errors.username}/>
                    <TextField
                        label="Email"
                        className="form-control"
                        value={values.email}
                        name={field_names.email}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        error={errors.email && touched.email}
                        helperText={(errors.email && touched.email) && errors.email}/>
                    <TextField
                        label="Password"
                        type="password"
                        className="form-control"
                        name={field_names.password}
                        value={values.password}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        error={errors.password && touched.password}
                        helperText={(errors.password && touched.password) && errors.password}/>
                    <TextField
                        label="Password"
                        type="password"
                        className="form-control"
                        name={field_names.password2}
                        value={values.password2}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        error={errors.password2 && touched.password2}
                        helperText={(errors.password2 && touched.password2) && errors.password2}/>
                    {this.props.open_applications_status === false ?
                        <TextField
                            label="Invitation Code"
                            type="text"
                            className="form-control"
                            name={field_names.invitation_code}
                            value={values.invitation_code}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={errors.invitation_code && touched.invitation_code}
                            helperText={(errors.invitation_code && touched.invitation_code) && errors.invitation_code}/> : null}
                    <CyberButton type="submit" onClick={() => {
                        if (errors.email || errors.username || errors.password || errors.password2)
                            playWarningSound()
                    }}>
                        Register
                    </CyberButton>
                    <p>Already have an account? <RouterLink to={LOGIN} onClick={playClickSound}
                                                            onMouseOver={playHoverSound}>Login</RouterLink></p>
                </Form>
            )}
        </Formik> : <div className="loading"><CircularProgress/></div>
    }
}

const mapStateToProps = state => ({
    user_loading: state.auth.user_loading,
    open_applications_status: state.auth.open_applications_status,
});

export default connect(mapStateToProps, {register})(RegisterForm)
