import {useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Grid from "@material-ui/core/Grid";
import RequiemDrawer from "./Drawer";
import {TargetSummary} from "./ActiveScrapers";
import React from "react";


/**
 *
 * @param {Object} props.requiem
 * @param {Array} props.targets
 * @param {Array} props.active_reports
 * @param {Number} props.active_reports_refresh_timeout
 * @returns {JSX.Element}
 * @constructor
 */
export function Dashboard(props) {
    const theme = useTheme();
    const smDown = useMediaQuery(theme.breakpoints.down('sm'));

    return <>
        <Grid item xs={12} className="welcome-container">
            <h5>Welcome to Cyber-Requiem</h5>
            <p><span>Timeout:</span> {props.active_reports_refresh_timeout / 1000} <span>seconds</span></p>
        </Grid>
        <Grid item xs={12} className="main-container">
            <Grid container justify={smDown ? 'center' : undefined}>
                {props.targets.map(target => <TargetSummary
                    requiem={props.requiem}
                    target={target}
                    active_reports={props.active_reports}
                />)}
            </Grid>
        </Grid>
    </>
}