import React, {Component, useState} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faCheck, faDownload, faEdit, faExpandArrowsAlt, faPlus, faTrash} from "@fortawesome/free-solid-svg-icons";

import {CyberButton} from "../../layout/common/common";
import DownloadTorrentForm from "./DownloadTorrentForm";
import {Form, Formik} from "formik";
import {Anime, AnimeOnServer, UserAnimeEntry} from "../../../models";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Checkbox from "@material-ui/core/Checkbox";
import {LANGUAGES} from "../../../globals";
import DialogActions from "@material-ui/core/DialogActions";
import {addAnimeToServer, updateAnimeToServer} from "../../../actions/requiem";
import {getAnimeOnServerInfo} from "../../../actions/anime";
import {isEmpty} from "../../functions";
import Loading from "../../layout/common/Loading";
import {CircularProgress} from "@material-ui/core";


class AnimeOnServerForm extends Component {
    static propTypes = {
        /** AnimeOnServer id */
        id: PropTypes.number.isRequired,
        /** Function to set the dialog status */
        setOpen: PropTypes.func.isRequired,
        /** Which dispatch function to call after successful update of the AnimeOnServer */
        alternative_callback: PropTypes.string,
        /** Should the form be for editing or adding, editing will try to fetch the server info for the ID sent */
        edit: PropTypes.bool,
    };

    constructor() {
        super(...arguments);
        this.toggleExpandLanguages = this.toggleExpandLanguages.bind(this);
        this.state = {
            open: false,
            expand_languages: false,
        };
    }

    componentDidMount() {
        this.props.getAnimeOnServerInfo(this.props.id)
    }

    toggleExpandLanguages() {
        this.setState({expand_languages: !this.state.expand_languages})
    }

    render() {
        if (this.props.edit && this.props.anime_on_server_loading)
            return <div className="loading-container"><CircularProgress color="secondary"/></div>

        const edit = !isEmpty(this.props.anime_on_server)
        const languages = this.state.expand_languages ? LANGUAGES : [LANGUAGES[0], LANGUAGES[1]];
        const field_names = {
            anime: 'anime',
            location: 'location',
            size_on_HDD_GB: 'size_on_HDD_GB',
            additional_info: 'additional_info',
            audio_channel: 'audio_channel',
            video_quality: 'video_quality',
            incomplete: 'incomplete',
            incomplete_progress: 'incomplete_progress',
        };
        let initial_values = {
            anime: this.props.id,
            location: '',
            size_on_HDD_GB: 0.00,
            additional_info: '',
            audio_channel: '2.0',
            video_quality: '1080p',
            incomplete: false,
            incomplete_progress: 0,
        };
        LANGUAGES.forEach((language) => {
            const lang = language.toLowerCase();
            initial_values[lang + '_dub'] = false;
            initial_values[lang + '_sub'] = false;
        });
        if (edit) {
            initial_values = {
                anime: this.props.id,
                location: this.props.anime_on_server[AnimeOnServer.location],
                size_on_HDD_GB: this.props.anime_on_server[AnimeOnServer.size_on_HDD_GB],
                additional_info: this.props.anime_on_server[AnimeOnServer.additional_info],
                audio_channel: this.props.anime_on_server[AnimeOnServer.audio_channel],
                video_quality: this.props.anime_on_server[AnimeOnServer.video_quality],
                incomplete: this.props.anime_on_server[AnimeOnServer.incomplete],
                incomplete_progress: this.props.anime_on_server[AnimeOnServer.incomplete_progress],
            };
            LANGUAGES.forEach((language) => {
                const lang = language.toLowerCase();
                initial_values[lang + '_dub'] = this.props.anime_on_server[`${lang}_dub`];
                initial_values[lang + '_sub'] = this.props.anime_on_server[`${lang}_sub`];
            });
        }

        return <>
            <DialogTitle id="form-dialog-title">{edit ? 'Edit' : 'Add'} anime to server</DialogTitle>
            <DialogContent>
                <Formik
                    initialValues={initial_values}
                    onSubmit={(values, {setSubmitting}) => {
                        if (edit)
                            this.props.updateAnimeToServer(values, this.props.alternative_callback);
                        else
                            this.props.addAnimeToServer(values, this.props.alternative_callback);
                        setSubmitting(false);
                    }}>
                    {({
                          isSubmitting, values, handleChange,
                          handleBlur
                      }) => (
                        <Form id="Edit-Anime-On-Server-Form">
                            <Grid container className="form-group">
                                <Grid item xs={12}>
                                    <TextField
                                        className="form-control"
                                        label="Location"
                                        color="secondary"
                                        autoComplete={false}
                                        name={field_names.location}
                                        value={values[field_names.location]}
                                        onChange={handleChange}
                                        onBlur={handleBlur}/>
                                </Grid>
                            </Grid>

                            <Grid container className="form-group">
                                <Grid item xs={2}>
                                    <TextField
                                        className="form-control"
                                        label="Size"
                                        color="secondary"
                                        type="number"
                                        name={field_names.size_on_HDD_GB}
                                        value={values[field_names.size_on_HDD_GB]}
                                        onChange={handleChange}
                                        onBlur={handleBlur}/>
                                </Grid>
                                <Grid item xs={5}>
                                    <InputLabel id="video-quality" className="input-label">Video
                                        Quality</InputLabel>
                                    <Select
                                        labelId="video-quality"
                                        className="form-control"
                                        color="secondary"
                                        value={values[field_names.video_quality]}
                                        name={field_names.video_quality}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    >
                                        <MenuItem value="2160p">2160p</MenuItem>
                                        <MenuItem value="1080p">1080p</MenuItem>
                                        <MenuItem value="720p">720p</MenuItem>
                                        <MenuItem value="580p">580p</MenuItem>
                                        <MenuItem value="480p">480p</MenuItem>
                                        <MenuItem value="360p">360p</MenuItem>
                                        <MenuItem value="240p">240p</MenuItem>
                                    </Select>
                                </Grid>
                                <Grid item xs={5}>
                                    <InputLabel id="audio-quality" className="input-label">Audio
                                        Channel</InputLabel>
                                    <Select
                                        labelId="audio-quality"
                                        className="form-control"
                                        color="secondary"
                                        value={values[field_names.audio_channel]}
                                        name={field_names.audio_channel}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    >
                                        <MenuItem value="2.0">2.0</MenuItem>
                                        <MenuItem value="5.1">5.1</MenuItem>
                                    </Select>
                                </Grid>
                            </Grid>

                            <Grid container className="form-group">
                                <Grid item xs={6}>
                                    <label>Incomplete</label>
                                    <Checkbox
                                        value={values[field_names.incomplete]}
                                        inputProps={{'aria-label': 'primary checkbox'}}
                                        name={field_names.incomplete}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                    />
                                </Grid>
                                <Grid item xs={6}>
                                    <TextField
                                        className="form-control"
                                        label="Incomplete Progress"
                                        color="secondary"
                                        type="number"
                                        disabled={!values[field_names.incomplete]}
                                        name={field_names.incomplete_progress}
                                        value={values[field_names.incomplete_progress]}
                                        onChange={handleChange}
                                        onBlur={handleBlur}/>
                                </Grid>
                            </Grid>

                            <Grid container className="form-group">
                                <Grid item xs={12}>
                                    <label>Dubs</label>
                                    <CyberButton classNames="expand-btn" onClick={this.toggleExpandLanguages}>
                                        <FontAwesomeIcon icon={faExpandArrowsAlt}/>
                                    </CyberButton>
                                </Grid>
                                {languages.map((language, x) => (
                                    <Grid item xs={3}>
                                        <div className="language-box" key={x}>
                                            <Checkbox
                                                value={values[language.toLowerCase() + '_dub']}
                                                checked={values[language.toLowerCase() + '_dub']}
                                                inputProps={{'aria-label': 'primary checkbox'}}
                                                name={language.toLowerCase() + '_dub'}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            />
                                            <label>{language}</label>
                                        </div>
                                    </Grid>
                                ))}
                            </Grid>

                            <Grid container className="form-group">
                                <Grid item xs={12}>
                                    <label>Subs</label>
                                </Grid>
                                {languages.map((language, x) => (
                                    <Grid item xs={3}>
                                        <div className="language-box" key={x}>
                                            <Checkbox
                                                value={values[language.toLowerCase() + '_sub']}
                                                checked={values[language.toLowerCase() + '_sub']}
                                                inputProps={{'aria-label': 'primary checkbox'}}
                                                name={language.toLowerCase() + '_sub'}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            />
                                            <label>{language}</label>
                                        </div>
                                    </Grid>
                                ))}
                            </Grid>

                            <Grid container className="form-group">
                                <Grid item xs={12}>
                                    <TextField
                                        className="form-control"
                                        label="Additional Info"
                                        color="secondary"
                                        variant="outlined"
                                        type="text"
                                        name={field_names.additional_info}
                                        value={values[field_names.additional_info]}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        multiline={true}
                                    />
                                </Grid>
                            </Grid>

                            <Grid container className="form-group">
                                <Grid item xs={12}>
                                    <CyberButton classNames="btn-success" variant="outlined" type="submit"
                                                 onClick={() => this.props.setOpen(false)} disabled={isSubmitting}>
                                        <FontAwesomeIcon icon={faCheck}/>
                                    </CyberButton>
                                </Grid>
                            </Grid>
                        </Form>
                    )}
                </Formik>
            </DialogContent>
        </>
    }
}

const mapStateToProps = state => ({
    anime_on_server: state.anime.anime_on_server,
    anime_on_server_loading: state.anime.anime_on_server_loading,
});

export default connect(mapStateToProps, {
    getAnimeOnServerInfo,
    addAnimeToServer,
    updateAnimeToServer
})(AnimeOnServerForm)
