import {BRANCH_ANIME, BRANCH_CHARACTERS, BRANCH_EROGE, BRANCH_MANGA, BRANCH_PEOPLE} from "../globals";
import {getAnime, getPendingAnimeServerInfos} from "./anime";
import {createMessage, returnErrors} from "./messages";
import {
    API_ADD_ANIME_TO_SERVER, API_APPROVE_ANIME_ON_SERVER, API_GET_ACTIVE_TORRENTS_PROGRESS,
    API_GET_NEWS,
    API_GET_REQUIEM, API_GET_SCRAPERS_PROGRESS,
    API_GET_TARGETS,
    API_OPEN_ANIME_FOLDER,
    API_OPEN_EROGE_FOLDER,
    API_REQUIEM_DELETE_ANIME_SERVER_INFO_AND_TORRENTS,
    API_REQUIEM_DOWNLOAD_ANIME_TORRENT,
    API_REQUIEM_GET_STORAGE_INFO,
    API_REQUIEM_GET_TORRENT_PROGRESS,
    API_REQUIEM_UPDATE_ANIME_MAL,
    API_REQUIEM_UPDATE_CHARACTER_MAL,
    API_REQUIEM_UPDATE_EROGE_HENTAIGAMER,
    API_REQUIEM_UPDATE_MANGA_MAL,
    API_REQUIEM_UPDATE_PERSON_MAL,
    API_UPDATE_ANIME_TO_SERVER
} from "./api_urls";
import {tokenConfig} from "./auth";
import {Alert, Anime} from "../models";
import {getManga} from "./manga";
import {getCharacter} from "./characters";
import {getEroge} from "./eroge";
import {
    ANIME_LOADING,
    CANCEL_ANIME_LOADING,
    CANCEL_CHARACTERS_LOADING,
    CANCEL_EROGE_LOADING,
    CANCEL_MANGA_LOADING,
    CANCEL_PEOPLE_LOADING,
    CHARACTERS_LOADING,
    DOWNLOAD_TORRENT,
    EROGE_LOADING,
    GET_ACTIVE_TORRENTS_PROGRESS, GET_ANIME,
    GET_NEWS, GET_PENDING_ANIME_SERVER_INFO,
    GET_REQUIEM,
    GET_SCRAPERS_PROGRESS,
    GET_STORAGE_INFO,
    GET_TARGETS,
    GET_TORRENT_PROGRESS,
    MANGA_LOADING,
    NEWS_LOADING,
    PEOPLE_LOADING,
    REQUIEM_LOADING
} from "./types";
import {getPerson} from "./people";


function scrapeUpdateTitleApiUrl(branch, id) {
    switch (branch) {
        case BRANCH_ANIME:
            return API_REQUIEM_UPDATE_ANIME_MAL(id);
        case BRANCH_MANGA:
            return API_REQUIEM_UPDATE_MANGA_MAL(id);
        case BRANCH_CHARACTERS:
            return API_REQUIEM_UPDATE_CHARACTER_MAL(id);
        case BRANCH_PEOPLE:
            return API_REQUIEM_UPDATE_PERSON_MAL(id);
        case BRANCH_EROGE:
            return API_REQUIEM_UPDATE_EROGE_HENTAIGAMER(id);
        default:
            return ''
    }
}


// GET REQUIEM MODEL
export const getRequiem = () => (dispatch, getState) => {
    dispatch({type: REQUIEM_LOADING});

    axios
        .get(API_GET_REQUIEM, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: GET_REQUIEM,
                payload: res.data
            })
        })
        .catch(err => {
            if (err.response) dispatch(returnErrors(err.response.data, err.response.status));
            else dispatch(returnErrors(err, 0));
        })
};


