import React, {useState} from 'react'

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {CyberButton} from "../../layout/common/common";
import {faCheck, faEdit, faPlus, faTrash, faUserEdit} from "@fortawesome/free-solid-svg-icons";
import {Dialog} from "@material-ui/core";
import AnimeOnServerForm from "./EditAnimeOnServerModalForm";
import {approveAnimeOnServer, deleteAnimeServerInfoAndTorrents, openAnimeFolder} from "../../../actions/requiem";
import store from "../../../store";
import {faFolder} from "@fortawesome/free-regular-svg-icons";
import {ADMIN_EDIT_ANIME_ON_SERVER} from "../../../urls";


/**
 *
 * @param {Object} props.id - ID of the anime itself
 * @param {String} props.classNames - ID of the anime itself
 * @param {String} props.alternative_callback - Which dispatch function to call after successful update of the AnimeOnServer
 * @param {Boolean} props.edit - Should the form be for editing or adding, editing will try to fetch the server
 *                               info for the ID sent
 * @returns {JSX.Element}
 * @constructor
 */
export function AnimeOnServerModalForm(props) {
    const [open, setOpen] = useState(false)

    return <div id="Edit-Anime-On-Server-Modal" className={props.classNames}>
        <CyberButton classNames="btn-contained-secondary" onClick={() => setOpen(true)}>
            <FontAwesomeIcon icon={props.edit ? faEdit : faPlus}/>
        </CyberButton>

        <Dialog open={open} onClose={() => setOpen(false)} aria-labelledby="form-dialog-title"
                className="edit-anime-on-server-dialog"
        >
            <AnimeOnServerForm
                id={props.id} setOpen={setOpen} alternative_callback={props.alternative_callback}
                edit={props.edit}
            />
        </Dialog>
    </div>
}


/**
 *
 * @param {Number} props.id
 * @returns {JSX.Element}
 * @constructor
 */
export function OpenAnimeFolderButton(props) {
    return <CyberButton classNames="btn-contained-secondary" onClick={() => {
        store.dispatch(openAnimeFolder(props.id));
     }}>
        <FontAwesomeIcon icon={faFolder}/>
    </CyberButton>
}


/**
 *
 * @param {Number} props.id
 * @returns {JSX.Element}
 * @constructor
 */
export function DeleteAnimeOnServerButton(props) {
    return <CyberButton classNames="btn-contained-danger" onClick={() => {
         if (window.confirm('Are you sure?'))
             store.dispatch(deleteAnimeServerInfoAndTorrents(props.id));
     }}>
        <FontAwesomeIcon icon={faTrash}/>
    </CyberButton>
}


/**
 *
 * @param {Number} props.id
 * @param {String} props.alternative_callback
 * @returns {JSX.Element}
 * @constructor
 */
export function ApproveAnimeOnServerButton(props) {
    return <CyberButton classNames="btn-contained-success" onClick={() => {
         if (window.confirm('Are you sure?'))
             store.dispatch(approveAnimeOnServer(props.id, props.alternative_callback));
     }}>
        <FontAwesomeIcon icon={faCheck}/>
    </CyberButton>
}


/**
 *
 * @param {Number} props.id
 * @returns {JSX.Element}
 * @constructor
 */
export function AdminEditAnimeOnServerButton(props) {
    return <a href={ADMIN_EDIT_ANIME_ON_SERVER(props.id)} className="margin-left-10">
        <CyberButton classNames="btn-contained-secondary" component="span">
            <FontAwesomeIcon icon={faUserEdit}/>
        </CyberButton>
    </a>
}
