import Grid from "@material-ui/core/Grid";
import {CyberLink} from "../../layout/common/common";
import {HOME} from "../../../urls";
import React, {useState} from "react";
import {TargetSummary} from "./ActiveScrapers";
import {useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import MiniDrawer from "./Drawer";
import SwipeableTemporaryDrawer from "./Drawer";
import RequiemDrawer from "./Drawer";
import {Dashboard} from "./Dashboard";
import Torrents from "./Torrents";

export class SidePanelTabs {
    static dashboard = 'Dashboard';
    static scrapers = 'Scrapers';
    static torrents = 'Torrents';
}


// noinspection HtmlUnknownTarget
export const GuestView = <Grid container className="not-authorized-container" justify="center">
    <Grid item xs={10} sm={6} md={4} lg={4} xl={3} className="not-authorized">
        <div className="box-light">
            <img src="/images/cyber-administration-logo.png"/>
            <h5>Only members of the <i>Cyber-Administration</i> group can access the Requiem system.</h5>
            <CyberLink to={HOME}>Home</CyberLink>
        </div>
    </Grid>
</Grid>


/**
 *
 * @param {Object} props.requiem
 * @param {Array} props.targets
 * @param {Array} props.active_reports
 * @param {Number} props.active_reports_refresh_timeout
 * @returns {JSX.Element}
 * @constructor
 */
export function AdminView(props) {
    const [selected_tab, setSelectedTab] = useState(SidePanelTabs.dashboard);

    return <Grid container className="authorized-container">
        <RequiemDrawer selected_tab={selected_tab} setSelectedTab={setSelectedTab}/>
        {selected_tab === SidePanelTabs.dashboard ? <Dashboard
            requiem={props.requiem}
            targets={props.targets}
            active_reports={props.active_reports}
            active_reports_refresh_timeout={props.active_reports_refresh_timeout}
        /> : null}
        {selected_tab === SidePanelTabs.torrents ? <Torrents/> : null}
    </Grid>
}
