import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from "prop-types";

import {getRequiem, getScrapersProgress, getTargets} from "../../../actions/requiem";
import CyberNavbar from "../../layout/navbar/CyberNavbar";
import CyberFooter from "../../layout/footer/CyberFooter";

import '../../../assets/css/requiem.css'
import Grid from "@material-ui/core/Grid";
import {Link} from "react-router-dom";
import {HOME} from "../../../urls";
import {CyberLink} from "../../layout/common/common";
import {Report, Target} from "../../../models";
import {TargetSummary} from "./ActiveScrapers";
import {isEmpty} from "../../functions";
import {AdminView, GuestView} from "./Components";


function getTimeoutFromReports(targets, active_reports) {
    let reports = {}
    let count = 0
    active_reports.forEach(report => (
        reports[report[Report.active_target]] = report
    ))
    targets.forEach(target => {
        if (target[Target.active] && target[Target.active_scrape])
            if (reports[target[Target.id]]?.[Report.error] === false && reports[target[Target.id]]?.[Report.finished] === false)
                count += 1
    })
    if (count === 0)
        count = 1
    return count * 10000
}


class RequiemHome extends Component {
    constructor() {
        super(...arguments);
        this.state = {
            get_scrapers_progress_interval: undefined,
            timeout: 0
        };
    }

    componentDidMount() {
        document.title = 'Cyber Requiem';
        window.scrollTo(0, 0);
        if (this.props.user.is_superuser) {
            this.props.getRequiem()
            this.props.getTargets()
            this.props.getScrapersProgress()
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.get_scrapers_progress_interval === undefined) {
            /**
             * Initialize Interval for fetching active reports, with dynamic timout
             */
            if (this.props.active_reports.length > 0 && this.props.targets.length > 0) {
                const timeout = getTimeoutFromReports(this.props.targets, this.props.active_reports)
                const interval = setInterval(this.props.getScrapersProgress, timeout);
                this.setState({get_scrapers_progress_interval: interval, timeout: timeout})
            }
        } else {
            /**
             * Check if the number of active scrapers has changed, and update the Interval timeout
             */
            const timeout = getTimeoutFromReports(this.props.targets, this.props.active_reports)
            if (prevState.timeout !== timeout) {
                clearInterval(this.state.get_scrapers_progress_interval)
                const interval = setInterval(this.props.getScrapersProgress, timeout);
                this.setState({get_scrapers_progress_interval: interval, timeout: timeout})
            }
        }
    }

    componentWillUnmount() {
        if (this.state.get_scrapers_progress_interval !== undefined)
            clearInterval(this.state.get_scrapers_progress_interval)
    }

    render() {
        // noinspection HtmlUnknownTarget
        return <div id="Requiem">
            <CyberNavbar classNames={this.props.user.is_superuser ? 'padded-navbar' : undefined}/>
            {this.props.user.is_superuser ? <AdminView
                requiem={this.props.requiem}
                targets={this.props.targets}
                active_reports={this.props.active_reports}
                active_reports_refresh_timeout={this.state.timeout}
            /> : GuestView}
            {this.props.user.is_superuser ? <CyberFooter/> : null}
        </div>
    }
}

const mapStateToProps = state => ({
    user: state.auth.user,
    requiem_is_loading: state.requiem.requiem_is_loading,
    requiem: state.requiem.requiem,
    targets: state.requiem.targets,
    active_reports: state.requiem.active_reports,
});

export default connect(mapStateToProps, {getRequiem, getTargets, getScrapersProgress})(RequiemHome)
